* {
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #fafafa;
	color: #212121;
	line-height: 1.5;
}

img {
	display: block;
	max-width: 100%;
}

.container {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.timer {
	margin-top: 30px;
	display: flex;
	gap: 20px;
}

.field {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.value {
	font-size: 24px;
	font-weight: bold;
}

#datetime-picker {
	width: 200px;
	padding: 8px 12px;
}

button {
	padding: 8px 12px;
	cursor: pointer;
	background-color: rgb(31, 115, 187);
	border-radius: 5px;
	color: #fafafa;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	font-weight: bold;
}
